@import url(https://fonts.googleapis.com/css?family=Joti+One);

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
html, body {
  overflow: hidden;
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
nav.fix.top.middle {
  position: fixed;
  top: 0;
  left: 15vw;
  color: white;
  z-index: 99999;
}
nav a {
  color: white;
  text-decoration: none !important;
  margin: 0 .5em;
  font-size: 18px;
}
nav a.router-link-exact-active {
  color: transparent;
}
.fix, .aplayer {
  position: fixed;
  top: 0;
  left: 0;
}
iframe {
  position: fixed;
  top: 0;
  right: 0;
}


.about{
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  background-color: black;
  width: 100vw;
  height: 100vh;
  color: white;
  font-size: 18px;
}
.about div {
  max-width: 420px;
  margin: 0 auto;
  text-align: left;
}
img {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: -2;
}
.big {
  font-size: 3em;
  color: white;
  text-decoration: none;
  border: 3px solid white;
  padding: .2em .5em;
  border-radius: 15px;
}



